import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import SecIntro from "./SecIntro";
import WhyChooseUsIco from "../assets/images/whyChooseUs.svg";
import { Link } from "react-router-dom";

const contentData = {
  heading:
    "We aren’t just any old driving school; we are a platform that connects learner drivers to their local driving instructor.",
  paragraph:
    "We allow you to search, compare and book accredited driving instructors online without needing to search for hours.",
  listItems: [
    "Search your own driving instructor",
    "Automatic and manual lessons available",
    "All instructors are licensed and certified in their state",
    "Driving instructor dual control vehicles",
    "Manage your bookings online 24/7",
    "Male and female instructors available",
    "Book into instructors calendars in real time",
    "Change your instructor online",
    "View each instructors profile   and reviews",
],
  buttonName: "Make a Booking >"
};

const introData = {
  titleStart: "Why Choose",
  highlight: "Us",
  titleEnd: "",
  buttonText: "Make a Booking >",
  buttonLink: "#",
  theme: "",
  buttonColor: "blue",
};

function WhyChooseUs() {
  return (
    <div className="WhyChooseUs">
      <Container>
        <Row>
          <Col lg={12}>
            {/* section intro block */}
            <SecIntro
              titleStart={introData.titleStart}
              highlight={introData.highlight}
              titleEnd={introData.titleEnd}
              buttonText={introData.buttonText}
              buttonLink={introData.buttonLink}
              theme={introData.theme}
              buttonColor={introData.buttonColor}
            />
            <div className="WhyChooseUs__wrp">
              <div className="WhyChooseUs__left">
                <h4>{contentData.heading}</h4>
                <p>{contentData.paragraph}</p>
                <div className="WhyChooseUs__points">
                  <ul>
                    {contentData.listItems.map((item, index) => (
                      <li key={index}>{item}</li>
                    ))}
                  </ul>
                  <Link to="/" className="btn btn-blue">
                    {contentData.buttonName}
                  </Link>
                </div>
              </div>
              <div className="WhyChooseUs__right">
                <img src={WhyChooseUsIco} alt="" />
              </div>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  );
}

export default WhyChooseUs;

