import React, { useEffect, useState, useRef } from "react";
import { Container, Row, Col } from "react-bootstrap";
import HomeFaqImg from "../assets/images/faq.png";
import SecIntro from "./SecIntro";
import { apiGet } from "../utilities/axiosInstance";
import API_ENDPOINTS from "../utilities/apiEndPoints";
import { isEmpty } from "../helpers";

const HomeFaqIntroData = {
  titleStart: "Learn More About ",
  highlight: "Our Company",
  titleEnd: "",
};

function HomeFaq() {
  const [faqsData, setFaqsData] = useState([]);
  const [activeIndex, setActiveIndex] = useState(0);
  const contentRef = useRef([]);

  useEffect(() => {
    const fetchFaqs = async () => {
      try {
        const response = await apiGet(API_ENDPOINTS.INFORMATIONS);
        setFaqsData(response.data); // Assuming response.data contains the FAQ data
      } catch (error) {
        console.error("Error fetching FAQs:", error);
      }
    };

    fetchFaqs();
  }, []);

  const accordianData = !isEmpty(faqsData)
    ? faqsData.slice(0, 6).map((faq) => ({
        title: faq.title,
        content: faq.description,
      }))
    : [];

  const handleToggle = (index) => {
    setActiveIndex((prevIndex) => (prevIndex === index ? null : index));
  };

  const getHeight = (index) => {
    return activeIndex === index && contentRef.current[index]
      ? `${contentRef.current[index].scrollHeight}px`
      : "0px";
  };

  useEffect(() => {
    if (contentRef.current[0]) {
      contentRef.current[0].style.height = `${contentRef.current[0].scrollHeight}px`;
    }
  }, []);

  return (
    <div className="HomeFaq grey-bg">
      <Container>
        <Row>
          <Col lg={12}>
            <div className="HomeFaq__wrp">
              <div className="HomeFaq__left">
                <img src={HomeFaqImg} alt="FAQ" />
              </div>
              <div className="HomeFaq__right">
                <SecIntro
                  titleStart={HomeFaqIntroData.titleStart}
                  highlight={HomeFaqIntroData.highlight}
                  titleEnd={HomeFaqIntroData.titleEnd}
                />
                <div className="accordian accordian__singleOpen md:mt-[50px] lg:mt-[90px]">
                  {!isEmpty(accordianData) ? (
                    accordianData.map((item, index) => (
                      <div
                        key={index}
                        className={`accordian__row flex flex-col py-[25px] ${
                          activeIndex === index ? "active" : ""
                        }`}
                      >
                        <div
                          className="accordian__title flex items-start justify-between"
                          onClick={() => handleToggle(index)}
                        >
                          <h4>{item.title}</h4>
                        </div>
                        <div
                          className="accordian__desc"
                          ref={(el) => (contentRef.current[index] = el)}
                          style={{
                            height: getHeight(index),
                            overflow: "hidden",
                            transition: "height 0.4s ease",
                          }}
                        >
                          <div dangerouslySetInnerHTML={{ __html: item.content }} />
                        </div>
                      </div>
                    ))
                  ) : (
                    <p>Loading data</p>
                  )}
                </div>
              </div>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  );
}

export default HomeFaq;
